import React, { Component } from 'react';

class Article extends Component{
    constructor() {
        super();
        this.state ={
            data: {
                "name": "Sample",
                "date": "11/30/2018",
                "body": "sample here"
            }
        }
    }

    componentDidMount(){
        this.gatherArticles();
    }

    gatherArticles(){
        const data = {
            "name": "Sample",
            "date": "11/30/2018",
            "body": "sample here"
        }
        this.setState({data: data})
    }

    render(){
        const data = this.state.data["name"];
        return(
            <div>
               {data}
            </div>
        )
    }
}

export default Article;